.footer {
  padding-bottom: 10px;
  &__inner {
    max-width: 1150px;
    margin: 0 auto;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
  }
  &__logo {
    width: 195px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__email,
  &__phone {
    font-size: 19px;
    line-height: 100%;
    font-weight: 500;
    &:hover {
      opacity: 0.7;
    }
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;
    margin-bottom: 50px;
    li a {
      font-size: 19px;
      line-height: 100%;
      font-weight: 500;
      &:hover {
        opacity: 0.7;
      }
    }
  }
  &__text {
    max-width: 740px;
    text-align: center;
    font-size: 12px;
    line-height: 100%;
    margin: 0 auto;
  }
  &__group2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 50px;
  }
  &__logo2 {
    width: 226px;
    margin: 12px 0 20px;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    padding-bottom: 25px;
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__group {
      flex-direction: column;
    }
    &__logo {
      order: -2;
    }
    &__phone {
      order: -1;
    }
    &__list {
      flex-direction: column;
      gap: 20px;
      margin-bottom: 40px;
    }
    &__group2 {
      margin-top: 40px;
    }
  }
}