.wrapper {
  position: relative;
  padding: 15px;
  overflow: hidden;
}
.btn-white__outline {
  position: relative;
  background: transparent;
  border: 2px solid #fff;
  border-radius: 16px;
  color: #fff;
  &:hover {
    background: #fff;
    color: #000;
    .btn-arrow {
      svg path {
        fill: #000;
      }
    }
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
  .btn-arrow {
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 10px;
    width: 18px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
}
.h2 {
  font-size: 50px;
  line-height: 100%;
  font-weight: 500;
}
.swiper-button-prev,
.swiper-button-next {
  width: 75px;
  height: 75px;
  background: transparent;
  border: 3px solid #E9E9E9;
  border-radius: 50%;
  opacity: 1 !important;
  &:after {
    display: none;
  }
  &:hover {
    background: #E9E9E9;
  }
  svg {
    max-width: 50%;
    width: auto;
    height: auto;
  }
}
.swiper-pagination-progressbar {
  top: unset !important;
  bottom: 0;
  width: 100% !important;
  height: 6px !important;
  background: #F0F0F0;
  border-radius: 30px;
  &-fill {
    background: #131313 !important;
    border-radius: 30px;
  }
}

@media screen and (max-width: 1400px) {
  .h2 {
    font-size: 40px;
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding: 0;
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 32px;
  }
}

@media screen and (max-width: 359px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 50px;
    height: 50px;
  }
}