.main {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 16px;
  padding: 315px 0 50px;
  color: #fff;
  &__subtitle {
    position: relative;
    padding-left: 96px;
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
    text-transform: uppercase;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 80px;
      height: 2px;
      background: #fff;
    }
  }
  &__title {
    position: relative;
    display: flex;
    align-items: start;
    font-size: 127px;
    line-height: 100%;
    font-weight: 500;
    margin-top: 60px;
    sup {
      font-size: 82px;
      margin-top: -15px;
    }
  }
  &__text {
    max-width: 665px;
    font-size: 19px;
    line-height: 160%;
    margin-top: 60px;
  }
  &__btn {
    height: 116px;
    display: inline-flex;
    padding: 5px 116px;
    margin-top: 60px;
    span {
      font-size: 19px;
    }
  }
  &__location {
    margin-top: 140px;
    span {
      background: #0E0E0E;
      border-radius: 16px;
      padding: 20px 46px;
      font-size: 18px;
      line-height: 100%;
      color: #F4F4F4;
    }
  }
}
.about {
  padding: 20px 0 45px;
  &__items {
    display: grid;
    grid-template-columns: 1.15fr 0.925fr 0.925fr;
    gap: 15px;
  }
  &__item {
    position: relative;
    &--big {
      grid-row: 1/3;
      .about__item-desc {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      .about__item-text {
        p {
          font-size: 16px;
        }
      }
    }
    &-img {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 16px;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    &-desc {
      position: absolute;
      z-index: 2;
      left: 0;
      bottom: 0;
      padding: 40px;
    }
    &-title {
      font-size: 29px;
      line-height: 100%;
      font-weight: 500;
      color: #fff;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 30px;
      p {
        font-size: 15px;
        line-height: 120%;
        color: #fff;
      }
    }
  }
  &__title {
    color: #fff;
  }
}
.layouts {
  padding: 45px 0 70px;
  &__subtitle {
    display: none;
  }
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper-slide {
    display: flex;
    justify-content: center;
    img {
      max-width: 100%;
    }
  }
  .swiper-button-prev {
    left: -30px;
  }
  .swiper-button-next {
    right: -30px;
  }
}
.works {
  padding: 70px 0 45px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 50px;
  }
  &__left {
    position: relative;
    z-index: 2;
    width: 445px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: -50px;
      width: 5000px;
      height: 100%;
      background: #fff;
      pointer-events: none;
    }
  }
  &__right {
    width: calc(100% - 495px);
  }
  &__group {
    position: relative;
    z-index: 2;
  }
  &__logo {
    width: 240px;
    img {
      width: 100%;
    }
  }
  &__text {
    margin-top: 40px;
    p {
      font-size: 19px;
      line-height: 140%;
      font-weight: 500;
    }
  }
  &__slider {
    position: relative;
  }
  .swiper {
    overflow: unset;
  }
  &__slider-nav {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 80px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin-top: 0;
  }
  .swiper-pagination {
    display: none;
  }
}
.work-card {
  &__img {
    img {
      width: 100%;
      border-radius: 16px;
    }
  }
  &__desc {
    margin-top: 40px;
  }
  &__title {
    font-size: 22px;
    line-height: 100%;
    font-weight: 700;
  }
  &__text {
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;
    margin-top: 30px;
  }
}
.map-section {
  padding: 45px 0 25px;
  .map {
    position: relative;
    height: 500px;
    .ymaps-2-1-79-map {
      border-radius: 16px;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 16px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .main {
    &__title {
      margin-top: 40px;
    }
    &__text {
      margin-top: 40px;
    }
    &__btn {
      margin-top: 40px;
    }
    &__location {
      margin-top: 75px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    padding-top: 250px;
    &__title {
      font-size: 86px;
      sup {
        font-size: 56px;
        margin-top: -10px;
      }
    }
    &__text {
      max-width: 500px;
      font-size: 16px;
    }
    &__btn {
      height: 77px;
      padding: 5px 77px;
    }
  }
  .about {
    &__items {
      grid-template-columns: 1.1fr 0.95fr 0.95fr;
    }
    &__item {
      &--big {
        .about__item-text {
          p {
            font-size: 14px;
          }
        }
      }
      &-desc {
        padding: 20px;
      }
      &-title {
        font-size: 24px;
      }
      &-text {
        margin-top: 15px;
        p {
          font-size: 14px;
        }
      }
    }
  }
  .layouts {
    padding: 45px 0;
  }
  .works {
    padding: 45px 0;
    &__inner {
      gap: 30px;
    }
    &__left {
      width: 400px;
      &:before {
        right: -30px;
      }
    }
    &__right {
      width: calc(100% - 430px);
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    padding-top: 200px;
    border-radius: 0;
  }
  .about {
    padding-left: 15px;
    padding-right: 15px;
    &__item {
      &--big {
        .about__item-text {
          p {
            font-size: 12px;
          }
        }
      }
      &-text {
        p {
          font-size: 12px;
        }
      }
    }
  }
  .layouts {
    .swiper-button-prev {
      left: -20px;
    }
    .swiper-button-next {
      right: -20px;
    }
  }
  .map-section {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 992px) {
  .main {
    position: relative;
    padding: 160px 0 30px;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.6);
      pointer-events: none;
    }
    &__inner {
      position: relative;
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__subtitle {
      padding: 0 0 25px 0;
      font-size: 14px;
      letter-spacing: 6.66px;
      &:before {
        top: unset;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
      }
    }
    &__title {
      margin-top: 15px;
    }
    &__text {
      max-width: 335px;
      font-size: 14px;
      line-height: 120%;
      margin-top: 25px;
    }
    &__btn {
      margin-top: 25px;
    }
    &__location {
      span {
        padding: 15px;
        font-size: 14px;
      }
    }
  }
  .about {
    padding: 20px 15px 25px;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
    &__item {
      &--big {
        .about__item-text {
          p {
            font-size: 14px;
          }
        }
      }
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
  .layouts {
    padding: 25px 0 45px;
    &__title {
      text-align: center;
    }
    &__subtitle {
      display: block;
      text-align: center;
      font-size: 12px;
      line-height: 100%;
      font-weight: 500;
      margin-top: 50px;
    }
    &__slider-nav {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;
      margin-top: 20px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      position: static;
      margin-top: 0;
    }
  }
  .works {
    &__inner {
      flex-direction: column;
      gap: 40px;
      align-items: start;
    }
    &__left {
      width: 100%;
      align-items: center;
      &:before {
        display: none;
      }
    }
    &__right {
      width: 100%;
    }
    &__group {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__text {
      max-width: 340px;
      margin: 40px auto 0 auto;
      p {
        font-size: 14px;
      }
    }
    &__slider {
      padding-bottom: 40px;
    }
    &__slider-nav {
      display: none;
    }
    .swiper-pagination {
      display: block;
    }
  }
  .work-card {
    &__desc {
      margin-top: 25px;
    }
    &__title {
      font-size: 16px;
    }
    &__text {
      font-size: 14px;
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 768px) {
  .about {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
    &__item {
      &-big {
        .about__item-text {
          p {
            font-size: 12px;
          }
        }
      }
      &-desc {
        padding: 15px;
      }
      &-title {
        font-size: 20px;
      }
      &-text {
        gap: 10px;
        margin-top: 10px;
        p {
          font-size: 12px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .about {
    padding-top: 0;
    padding-left: 0;
    padding-right: 0;
    &__items {
      grid-template-columns: 100%;
      gap: 15px;
    }
    &__item {
      &--big {
        .about__item-title {
          text-align: center;
        }
        .about__item-text {
          display: grid;
          p {
            font-size: 14px;
          }
        }
      }
      &-img {
        img {
          border-radius: 0;
        }
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
      &-title {
        font-size: 16px;
      }
      &-text {
        display: none;
        p {
          font-size: 14px;
        }
      }
    }
  }
  .layouts {
    .swiper-slide {
      display: flex;
      justify-content: center;
      overflow: hidden;
      img {
        min-width: 650px;
      }
    }
  }
  .map-section {
    padding-left: 0;
    padding-right: 0;
    .map {
      .ymaps-2-1-79-map {
        border-radius: 0;
      }
      .ymaps-2-1-79-inner-panes {
        border-radius: 0;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .main {
    &__title {
      font-size: 70px;
      sup {
        font-size: 35px;
        margin-top: -12px;
      }
    }
  }
}