.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  padding: 15px 0;
  color: #fff;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
  }
  &__logo {
    width: 196px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 120px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 60px;
    li a {
      font-size: 19px;
      line-height: 100%;
      font-weight: 500;
      &:hover {
        opacity: 0.7;
      }
    }
  }
  &__phone {
    position: relative;
    padding-left: 20px;
    font-size: 19px;
    line-height: 100%;
    font-weight: 500;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 8px;
      height: 8px;
      background: #fff;
      border-radius: 50%;
    }
    &:hover {
      opacity: 0.7;
    }
  }
  &__email {
    font-size: 19px;
    line-height: 100%;
    font-weight: 500;
  }
}
.burger {
  position: absolute;
  z-index: 15;
  top: 10px;
  right: 0;
  width: 30px;
  height: 20px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #fff;
    border-radius: 3px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 9px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 9px;
      transform: rotate(45deg);
    }
  }
}
.header-mob {
  display: none;
}

@media screen and (max-width: 1400px) {
  .header {
    &__inner {
      gap: 30px;
    }
    &__group {
      gap: 30px;
    }
    &__list {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__logo {
      width: 120px;
    }
    @media screen and (min-width: 993px) {
      &__list {
        li a {
          font-size: 16px;
        }
      }
      &__phone {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__inner {
      flex-direction: column;
      gap: 15px;
    }
    &__list {
      display: none;
    }
  }
  .burger {
    display: block;
  }
  .header-mob {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    background: rgba(0, 0, 0, 0.8);
    padding: 15px 15px 40px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
    &__group {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 40px;
    }
    .header__list {
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
    .header__phone {
      padding: 0;
      &:before {
        display: none;
      }
    }
  }
}